import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'TextAnalyzerView',
  metaInfo: {
    // æ ‡é¢˜ï¼šçªå‡ºæ ¸å¿ƒåŠŸèƒ½ï¼ˆç»Ÿè®¡ã€åˆ†æï¼‰ã€è¦†ç›–çš„ç»´åº¦ï¼ˆå­—æ•°ã€è¯é¢‘ã€å¯è¯»æ€§ï¼‰å’Œæ”¯æŒçš„è¯­è¨€
    title: 'æ–‡æœ¬ç»Ÿè®¡åˆ†æå™¨ - åœ¨çº¿å­—æ•°ç»Ÿè®¡ã€è¯é¢‘åˆ†æã€å¯è¯»æ€§è¯„ä¼°å·¥å…· | æ”¯æŒä¸­è‹±æ–‡æ··åˆ',

    meta: [
      // æè¿°ï¼šè¯¦ç»†è¯´æ˜å·¥å…·èƒ½æä¾›çš„æŠ¥å‘Šå†…å®¹ï¼Œå¼ºè°ƒå…¨é¢æ€§å’Œåº”ç”¨ä»·å€¼ï¼ˆå†™ä½œã€SEOã€å†…å®¹ä¼˜åŒ–ï¼‰
      {
        name: 'description',
        content: 'å…è´¹åœ¨çº¿æ–‡æœ¬åˆ†æå·¥å…·ï¼Œæä¾›ç²¾å‡†çš„å­—æ•°ã€å­—ç¬¦æ•°ã€å¥å­æ•°ã€æ®µè½æ•°ç»Ÿè®¡ã€‚ä¸“ä¸šè¯é¢‘åˆ†æå’Œå†…å®¹å¯è¯»æ€§è¯„ä¼°ï¼Œå¸®åŠ©æ‚¨ä¼˜åŒ–æ–‡ç« ç»“æ„ã€æå‡å†…å®¹è´¨é‡ã€‚å®Œç¾æ”¯æŒä¸­è‹±æ–‡æ··åˆæ–‡æœ¬ã€‚'
      },

      // å…³é”®è¯ï¼šè¦†ç›–æ ¸å¿ƒåŠŸèƒ½ã€åˆ†æç»´åº¦ã€åº”ç”¨åœºæ™¯å’Œæ”¯æŒçš„è¯­è¨€
      {
        name: 'keywords',
        content: 'æ–‡æœ¬ç»Ÿè®¡åˆ†æ, å­—æ•°ç»Ÿè®¡, è¯é¢‘åˆ†æ, å¯è¯»æ€§è¯„ä¼°, åœ¨çº¿æ–‡æœ¬å·¥å…·, å†…å®¹ä¼˜åŒ–, ä¸­è‹±æ–‡æ··åˆåˆ†æ, æ–‡æœ¬æŠ¥å‘Š'
      }
    ]
  },
  data() {
    return {
      inputText: '',
      stats: {
        totalChars: 0,
        totalCharsNoSpaces: 0,
        chineseChars: 0,
        englishWords: 0,
        sentences: 0,
        paragraphs: 0,
        lines: 0,
        numbers: 0,
        punctuation: 0,
        englishChars: 0
      },
      wordFrequency: [],
      sampleTexts: [
        'è¿™æ˜¯ä¸€ä¸ªæ–‡æœ¬åˆ†æå·¥å…·çš„ç¤ºä¾‹æ–‡æœ¬ã€‚å®ƒå¯ä»¥åˆ†æä¸­æ–‡å’ŒEnglishæ··åˆçš„æ–‡æœ¬å†…å®¹ï¼Œç»Ÿè®¡å­—æ•°ã€è¯é¢‘ç­‰ä¿¡æ¯ã€‚This tool can analyze both Chinese and English text, providing comprehensive statistics and readability analysis.',
        'äººå·¥æ™ºèƒ½ï¼ˆArtificial Intelligenceï¼ŒAIï¼‰æ˜¯è®¡ç®—æœºç§‘å­¦çš„ä¸€ä¸ªåˆ†æ”¯ï¼Œå®ƒä¼å›¾äº†è§£æ™ºèƒ½çš„å®è´¨ï¼Œå¹¶ç”Ÿäº§å‡ºä¸€ç§æ–°çš„èƒ½ä»¥äººç±»æ™ºèƒ½ç›¸ä¼¼çš„æ–¹å¼åšå‡ºååº”çš„æ™ºèƒ½æœºå™¨ã€‚è¯¥é¢†åŸŸçš„ç ”ç©¶åŒ…æ‹¬æœºå™¨äººã€è¯­è¨€è¯†åˆ«ã€å›¾åƒè¯†åˆ«ã€è‡ªç„¶è¯­è¨€å¤„ç†å’Œä¸“å®¶ç³»ç»Ÿç­‰ã€‚',
        'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
      ]
    }
  },
  computed: {
    readingTime() {
      // ä¸­æ–‡æŒ‰300å­—/åˆ†é’Ÿï¼Œè‹±æ–‡æŒ‰200è¯/åˆ†é’Ÿè®¡ç®—
      const chineseTime = this.stats.chineseChars / 300
      const englishTime = this.stats.englishWords / 200
      return Math.max(1, Math.ceil(chineseTime + englishTime))
    },
    chinesePercentage() {
      return this.stats.totalChars > 0 ? (this.stats.chineseChars / this.stats.totalChars * 100) : 0
    },
    englishPercentage() {
      return this.stats.totalChars > 0 ? (this.stats.englishChars / this.stats.totalChars * 100) : 0
    },
    numberPercentage() {
      return this.stats.totalChars > 0 ? (this.stats.numbers / this.stats.totalChars * 100) : 0
    },
    punctuationPercentage() {
      return this.stats.totalChars > 0 ? (this.stats.punctuation / this.stats.totalChars * 100) : 0
    },
    avgSentenceLength() {
      return this.stats.sentences > 0 ? this.stats.totalCharsNoSpaces / this.stats.sentences : 0
    },
    avgWordLength() {
      const totalWords = this.stats.englishWords + this.stats.chineseChars
      return totalWords > 0 ? this.stats.totalCharsNoSpaces / totalWords : 0
    },
    complexityLevel() {
      const avgSentLen = this.avgSentenceLength
      if (avgSentLen < 15) return 'ç®€å•'
      if (avgSentLen < 25) return 'ä¸­ç­‰'
      if (avgSentLen < 35) return 'å¤æ‚'
      return 'å¾ˆå¤æ‚'
    },
    complexityType() {
      const level = this.complexityLevel
      if (level === 'ç®€å•') return 'success'
      if (level === 'ä¸­ç­‰') return 'warning'
      return 'danger'
    },
    readingLevel() {
      const avgSentLen = this.avgSentenceLength
      if (avgSentLen < 15) return 'å°å­¦æ°´å¹³'
      if (avgSentLen < 25) return 'ä¸­å­¦æ°´å¹³'
      if (avgSentLen < 35) return 'å¤§å­¦æ°´å¹³'
      return 'ä¸“ä¸šæ°´å¹³'
    }
  },
  methods: {
    analyzeText() {
      const text = this.inputText
      
      // åŸºç¡€ç»Ÿè®¡
      this.stats.totalChars = text.length
      this.stats.totalCharsNoSpaces = text.replace(/\s/g, '').length
      
      // ä¸­æ–‡å­—ç¬¦ç»Ÿè®¡
      this.stats.chineseChars = (text.match(/[\u4e00-\u9fa5]/g) || []).length
      
      // è‹±æ–‡å­—ç¬¦ç»Ÿè®¡
      this.stats.englishChars = (text.match(/[a-zA-Z]/g) || []).length
      
      // è‹±æ–‡å•è¯ç»Ÿè®¡
      this.stats.englishWords = (text.match(/\b[a-zA-Z]+\b/g) || []).length
      
      // æ•°å­—ç»Ÿè®¡
      this.stats.numbers = (text.match(/\d/g) || []).length
      
      // æ ‡ç‚¹ç¬¦å·ç»Ÿè®¡
      this.stats.punctuation = (text.match(/[^\w\s\u4e00-\u9fa5]/g) || []).length
      
      // å¥å­ç»Ÿè®¡
      this.stats.sentences = (text.match(/[.!?ã€‚ï¼ï¼Ÿ]+/g) || []).length || (text.trim() ? 1 : 0)
      
      // æ®µè½ç»Ÿè®¡
      this.stats.paragraphs = text.split(/\n\s*\n/).filter(p => p.trim()).length
      
      // è¡Œæ•°ç»Ÿè®¡
      this.stats.lines = text.split('\n').length
      
      // è¯é¢‘åˆ†æ
      this.analyzeWordFrequency()
    },
    
    analyzeWordFrequency() {
      const text = this.inputText.toLowerCase()
      const words = []
      
      // æå–è‹±æ–‡å•è¯
      const englishWords = text.match(/\b[a-zA-Z]+\b/g) || []
      words.push(...englishWords)
      
      // æå–ä¸­æ–‡è¯æ±‡ï¼ˆç®€å•æŒ‰å­—ç¬¦åˆ†å‰²ï¼‰
      const chineseChars = text.match(/[\u4e00-\u9fa5]/g) || []
      words.push(...chineseChars)
      
      // ç»Ÿè®¡è¯é¢‘
      const frequency = {}
      words.forEach(word => {
        if (word.length > 1 || /[\u4e00-\u9fa5]/.test(word)) {
          frequency[word] = (frequency[word] || 0) + 1
        }
      })
      
      // æ’åº
      this.wordFrequency = Object.entries(frequency)
        .map(([word, count]) => ({ word, count }))
        .sort((a, b) => b.count - a.count)
    },
    
    clearText() {
      this.inputText = ''
      this.analyzeText()
    },
    
    loadSampleText() {
      const randomIndex = Math.floor(Math.random() * this.sampleTexts.length)
      this.inputText = this.sampleTexts[randomIndex]
      this.analyzeText()
    },
    
    handleFileUpload(file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.inputText = e.target.result
        this.analyzeText()
        this.$message.success('æ–‡ä»¶ä¸Šä¼ æˆåŠŸï¼')
      }
      reader.onerror = () => {
        this.$message.error('æ–‡ä»¶è¯»å–å¤±è´¥ï¼')
      }
      reader.readAsText(file.raw, 'UTF-8')
    },
    
    exportToTxt() {
      const results = this.generateAnalysisReport()
      const filename = DownloadUtil.generateTimestampFilename('æ–‡æœ¬åˆ†ææŠ¥å‘Š', 'txt')
      DownloadUtil.downloadText(results, filename)
      this.$message.success('TXTæ–‡ä»¶å¯¼å‡ºæˆåŠŸï¼')
    },
    
    exportToJson() {
      const data = {
        originalText: this.inputText,
        analysisTime: new Date().toISOString(),
        statistics: this.stats,
        wordFrequency: this.wordFrequency.slice(0, 50),
        readability: {
          avgSentenceLength: this.avgSentenceLength,
          avgWordLength: this.avgWordLength,
          complexityLevel: this.complexityLevel,
          readingLevel: this.readingLevel,
          readingTime: this.readingTime
        },
        distribution: {
          chinese: this.chinesePercentage,
          english: this.englishPercentage,
          number: this.numberPercentage,
          punctuation: this.punctuationPercentage
        }
      }
      
      const filename = DownloadUtil.generateTimestampFilename('æ–‡æœ¬åˆ†ææ•°æ®', 'json')
      DownloadUtil.downloadJSON(data, filename)
      this.$message.success('JSONæ–‡ä»¶å¯¼å‡ºæˆåŠŸï¼')
    },
    
    async copyResults() {
      const results = this.generateAnalysisReport()
      try {
        const success = await ClipboardUtil.copyText(results)
        if (success) {
          this.$message.success('åˆ†æç»“æœå·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥ï¼Œè¯·æ‰‹åŠ¨å¤åˆ¶ï¼')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },
    
    generateAnalysisReport() {
      return `æ–‡æœ¬åˆ†ææŠ¥å‘Š
ç”Ÿæˆæ—¶é—´: ${new Date().toLocaleString()}

=== åŸºç¡€ç»Ÿè®¡ ===
æ€»å­—ç¬¦æ•°: ${this.stats.totalChars}
ä¸å«ç©ºæ ¼å­—ç¬¦æ•°: ${this.stats.totalCharsNoSpaces}
ä¸­æ–‡å­—ç¬¦æ•°: ${this.stats.chineseChars}
è‹±æ–‡å•è¯æ•°: ${this.stats.englishWords}
å¥å­æ•°: ${this.stats.sentences}
æ®µè½æ•°: ${this.stats.paragraphs}
è¡Œæ•°: ${this.stats.lines}
é¢„è®¡é˜…è¯»æ—¶é—´: ${this.readingTime}åˆ†é’Ÿ

=== å­—ç¬¦åˆ†å¸ƒ ===
ä¸­æ–‡å­—ç¬¦: ${this.chinesePercentage.toFixed(1)}%
è‹±æ–‡å­—ç¬¦: ${this.englishPercentage.toFixed(1)}%
æ•°å­—å­—ç¬¦: ${this.numberPercentage.toFixed(1)}%
æ ‡ç‚¹ç¬¦å·: ${this.punctuationPercentage.toFixed(1)}%

=== å¯è¯»æ€§åˆ†æ ===
å¹³å‡å¥é•¿: ${this.avgSentenceLength.toFixed(1)} å­—ç¬¦
å¹³å‡è¯é•¿: ${this.avgWordLength.toFixed(1)} å­—ç¬¦
å¤æ‚åº¦è¯„çº§: ${this.complexityLevel}
å»ºè®®é˜…è¯»æ°´å¹³: ${this.readingLevel}

=== è¯é¢‘ç»Ÿè®¡ (å‰20ä¸ª) ===
${this.wordFrequency.slice(0, 20).map((item, index) => 
  `${index + 1}. ${item.word}: ${item.count}æ¬¡`
).join('\n')}

=== åŸæ–‡æœ¬ ===
${this.inputText}`
    }
  }
